* {
  box-sizing: border-box;
  text-decoration: none;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell,
    'Open Sans', 'Helvetica Neue', sans-serif;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
}

.photo-card {
  overflow: hidden;
}
.photo-card img {
  display: block;
  width: 100%;
  height: 200px;
  object-fit: cover;
}

.photo-card a {
  text-decoration: none;
}

.search-form {
  display: flex;
  justify-content: center;
  padding: 8px;
  margin-bottom: 24px;
}

.load-more {
  display: block;
  margin-left: auto;
  margin-right: auto;
  padding: 20px 40px;
  font-size: 16px;
  font-weight: 700;
  background-color: rgb(40, 164, 247);
}

.load-more.is-hidden {
  display: none;
}

.gallery {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
  grid-gap: 20px;
  padding: 0;
  margin: 0;
  max-width: calc(100vw - 80px);
  margin-left: auto;
  margin-right: auto;
  margin-bottom: 30px;
}

.info {
  font-size: 12px;
  font-weight: 400;
  text-align: center;
}
